//main Variables
//Header line
.header-line-down {
  @header-line-down-color: darken(@brand-danger, 5%);
  border-bottom: @header-line-down-color 1px solid;
}

.header-line-up {
  @header-line-up: lighten(@brand-danger, 5%);
  border-top: @header-line-up 1px solid;
}

.container {
  width: 1200px !important;
}

//HEADER CUSTOM STYLES
.logo {
  width: 105px;
  height: 65px;
}

.header-top {
  .btn-danger {
    background-color: @brand-danger-dark;
    border-top-color: lighten(@brand-danger-dark, 15%);
    border-left-color: lighten(@brand-danger-dark, 15%);
    border-bottom-color: darken(@brand-danger-dark, 5%);
    border-right-color: darken(@brand-danger-dark, 5%);
  }
  .header-line-down;
  background-color: @brand-danger;
  padding:15px 0;
  h4 {
    color: @color-white;
    margin-top: 3px;
  }
  a {
    color: @color-white;
  }
}

nav {
