      width: 220px;
    }
    #alba {
      height: 105px;
      width: 180px;
    }
  }
  .footer-down {
    font-size: @font-size-h6;
    background-color: @color-footer-dark;
    padding: 10px 0;
    box-shadow: inset 0 5px 5px -5px rgba(0, 0, 0, 0.5);
    vertical-align: bottom;
    .col-xs-3 {
      a {
        display: inline-block;
        padding-top: 8px;
      }
    }
  }
  .no-gutter {
    padding-right: 0;
    padding-left: 0;
  }
  /* columns of same height styles */
  
  .row-full-height {
    height: 100%;
  }
  .col-full-height {
    height: 100%;
    vertical-align: top;
    /*middle default*/
  }
  .row-same-height {
    display: table;
    width: 100%;
    /* fix overflow */
    
    table-layout: fixed;
  }
  .col-xs-height {
    display: table-cell;
    float: none !important;
  }
  @media (min-width: 768px) {
    .col-sm-height {
      display: table-cell;
      float: none !important;
    }
  }
  @media (min-width: 992px) {
    .col-md-height {
      display: table-cell;
      float: none !important;
    }
  }
  @media (min-width: 1200px) {
    .col-lg-height {
      display: table-cell;
      float: none !important;
    }
  }
  /* vertical alignment styles */
  
  .col-top {
    vertical-align: top;
  }
  .col-middle {
    vertical-align: middle;
  }
  .col-bottom {
    vertical-align: bottom;
  }
  .article-prev {
    background-color: @background-gray;
    padding: 0;
    height: 100%;
    padding-bottom: 60px;
    .media-body {
      padding-left: 15px;
      padding-right: 15px;
    }
  }
  /***********COLLAPSE*********************/
  
  .collapse-block {
    margin-bottom: 30px;
    .glyphicon {
      color: @brand-danger;
    }
  }
  .collapse-heading > a {
    &:hover {
      text-decoration: none;
    }
    &:focus {
      text-decoration: none;
    }
    &:after {
      font-family: 'Glyphicons Halflings';
      content: '\e253';
      /*triangle top*/
      
      margin-top: 5px;
      padding-left: 15px;
    }
  }
  .collapse-heading > a.collapsed {
    &:after {
      content: '\e252';
      /*triangle bottom*/
    }
  }
  .collapse-list {
    li.active {
      & > a {
        color: black;
      }
    }
    ul {
      margin-left: 35px;
      li {
        padding: 10px 0;
        &:first-child {
          margin-top: 15px;
        }
      }
    }
  }
  .projects-short {
    p {
      font-size: @font-size-h3;
    }
  }
  .projects-full-info {
    p {
      padding-bottom: 30px;
    }
    ul {
      padding-left: 0px;
    }
    li {
      padding: 12.5px 0;
      list-style-type: none;
      &:before {
        content: "";
        color: @brand-danger;
        padding-right: 10px;
      }
    }
  }
  .content-line-light-2 {
    border-bottom: @color-gray 2px solid;
    padding-bottom: 60px;
  }
  /*************NEWS****************/
  
  .news-aside,
  .learning-aside {
    .collapse-block {
      padding-left: 35px;
    }
  }
  .news-list-body {
    .news-list-head {
      color: @brand-danger;
    }
    font-size: @font-size-h3;
    .news-date {
      font-size: @font-size-h4;
      color: black;
      padding-bottom: 25px;
    }
    .news-list-content {
      border-bottom: @color-gray 2px solid;
      padding-bottom: 60px;
      p {
        padding-bottom: 25px;
      }
    }
  }
  .pagination > li > a {
    padding: 0 3px;
  }
  .learning-sales {
    background-color: @background-gray;
    font-size: @font-size-h3;
    padding-bottom: 60px;
  }
  .learning-content {
    .projects-full-info;
    .h3;
  }
  .learning-request {
    padding-top: 30px;
  }
  .programs-icons {
    display: block;
    width: 35px;
    height: 35px;
    background-size: 35px;
  }
  #kursor {
    background-image: url(../img/kursor_pink.svg);
    &:hover {
      background-image: url(../img/kursor_grey.svg);
    }
  }
  #play {
    background-image: url(../img/play_pink.svg);
    &:hover {
      background-image: url(../img/play_grey.svg);
    }
  }
  #monitor {
    background-image: url(../img/presentation_pink.svg);
    &:hover {
      background-image: url(../img/presentation_grey.svg);
    }
  }
  #call {
    background-image: url(../img/call_pink.svg);
    &:hover {
      background-image: url(../img/call_grey.svg);
    }
  }
.request,.learning-request{
  padding: 45px 0;
  .form-group{
    .form-control {
      display: block;
      width: 100%;
      height: @input-height-base; // Make inputs at least the height of their button counterpart (base line-height + padding + border)
      padding: @padding-base-vertical @padding-base-horizontal;
      font-size: @font-size-base;
      line-height: @line-height-base;
      color: @input-color;
      background-color: #EEEFEF;
      background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
      border: 1px solid @input-border;
      border-radius: @input-border-radius; // Note: This has no effect on <select>s in some browsers, due to the limited stylability of <select>s in CSS.
      .box-shadow(inset 1px 1px 1px 0 rgba(0,0,0,0.2));
      .transition(~"border-color ease-in-out .15s, box-shadow ease-in-out .15s");

  }
  textarea.form-control{
      height: auto;
    }
  button{
    margin-top: 25px;
  }
}
}